/* Основные стили для модального окна */
.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background: rgba(0, 0, 0, 0.5);
    overflow-x: hidden; /* Отключаем горизонтальную прокрутку */
  }
  
  /* Стиль для контента модального окна */
  .modalContent {
    position: relative;
    background-color: rgb(27, 27, 27);
    padding: 20px;
    border-radius: 8px;
    width: 400px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-height: 80vh;
    overflow-y: auto; /* Вертикальная прокрутка, если контента слишком много */
    overflow-x: hidden; /* Отключаем горизонтальную прокрутку */
  }
  
  /* Стиль для кнопки "X" */
  .closeButton {
    position: absolute;
    top: 10px;
    right: 20px;
    background-color: transparent;
    color: white;
    border: none;
    cursor: pointer;
    z-index: 100;
  }
  
  .closeButton:hover {
    background-color: rgba(255, 255, 255, 0.2);
  }
  
  /* Контейнер для кнопок */
  .buttonContainer {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  